const Carousel = () => {
  const [current, setCurrent] = useState(0);

  useEffect(()=> {
    const next = (current + 1) % images.length;
    const id = setTimeout(()=> setCurrent(next), 5000);
    console.log(current);
    return ()=> clearTimeout(id);
  },[current])
  return(
    <div className="main">

      <style jsx>{`
        .main{
          height: 100vh;
          overflow: hidden;
          display: flex;
        }
      `}
      </style>
    </div>
  )
}

const Main = () => {

  return(
    <Layout>
      <Hero />
      <Carousel />
    </Layout>
  )
}

export default Main;
